<template>
  <div :class="accordionClasses">
    <div class="accordion-controls flex justify-between items-center p-2">
      <h2 class="text-xl font-semibold">{{ title }}</h2>
      <button @click="toggleAll" class="text-sm font-medium">{{ allOpen ? 'Hide all' : 'Show all' }}</button>
    </div>
    <div v-for="(item, index) in items" :key="index" :class="itemClasses" @click="toggle(index)">
      <div class="accordion-header flex justify-between items-center cursor-pointer p-4">
        <h3 :class="headerClasses">{{ item.title }}</h3>
        <span :class="chevronClasses">{{ isOpen(index) ? '▲' : '▼' }}</span>
      </div>
      <div v-if="isOpen(index)" class="accordion-content p-4">
        <slot :name="`content-${index}`">{{ item.content }}</slot>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Accordion',
  props: {
    items: {
      type: Array,
      required: true,
    },
    variant: {
      type: String,
      default: 'default', // default, bordered, highlighted, collapsible
    },
    title: {
      type: String,
      default: 'Accordion',
    },
  },
  data() {
    return {
      openIndex: [],
      allOpen: false,
    };
  },
  computed: {
    accordionClasses() {
      return [
        'accordion',
        'space-y-2',
        this.variantClasses,
      ];
    },
    itemClasses() {
      const baseClasses = [
        'accordion-item',
        'transition-all',
        'duration-300',
        'ease-in-out',
      ];
      return baseClasses;
    },
    headerClasses() {
      return [
        'accordion-header',
        'font-semibold',
      ];
    },
    chevronClasses() {
      return [
        'accordion-chevron',
        'text-xl',
        'transition-transform',
      ];
    },
    variantClasses() {
      return {
        default: '',
        bordered: 'border border-grey-light',
        highlighted: 'bg-intrepid-red-light text-white',
        collapsible: 'bg-midnight-light text-white',
      }[this.variant] || '';
    },
  },
  methods: {
    toggle(index) {
      if (this.openIndex.includes(index)) {
        this.openIndex = this.openIndex.filter(i => i !== index);
      } else {
        this.openIndex.push(index);
      }
    },
    isOpen(index) {
      return this.openIndex.includes(index);
    },
    toggleAll() {
      if (this.allOpen) {
        this.openIndex = [];
      } else {
        this.openIndex = this.items.map((_, index) => index);
      }
      this.allOpen = !this.allOpen;
    },
  },
};
</script>

<style scoped>
.accordion-item:hover {
  @apply bg-grey-lighter-alpha;
}
</style>
